import React, { Component } from 'react';
import './chapter.scss';
import Config from '../../config/Config';
import axios from 'axios';
import InnerList from '../Contents/List/InnerList/InnerList';
import {Link} from 'react-router-dom';
const conf = new Config();

class Chapter extends Component {

    state = { photos: [] , book: {content: []} }

    componentDidMount() {
        let query = "blue sky";
        let per_page = 50;
        let page = 1;

        axios({
            method: 'GET',
            url: `${conf.pexels.url}search?query=${query}&per_page=${per_page}&page=${page}`,
            headers: {
                Authorization: conf.pexels.key
            }
        }).then(resp => {
            this.setState({
                photos: resp.data.photos
            })
        });
        this.getBook()

    }

    getBook() {
        let book = localStorage.getItem("book");
        if (book === null) {
            axios.get(conf.book).then(resp => {
                this.setState({
                    book: resp.data
                });
                localStorage.setItem("book", JSON.stringify(resp.data))
            })
        } else {
            this.setState({
                book: JSON.parse(book)
            });
        }
    }

    render = () => {
        return (
            <div>
                {this.state.photos.length > 0 &&
                    <div className="container-fluid splash" style={{
                        backgroundImage: `url(${this.state.photos[Math.floor(Math.random() * 50) + 1].src.square})`
                    }}>
                    </div>
                }
                {this.state.book.content.length > 0 && 
                <div className="front-chapter">
                    <div className="actions">
                        <Link to="/contents">
                            Back to contents
                        </Link>
                    </div>
                    <div className="chapter-head">
                        <h4>
                            {this.state.book.content[this.props.match.params.index - 1].section}
                        </h4>
                    </div>
                    <div className="time"></div>
                    <div className="sub-chapters">
                        <InnerList 
                            pi={parseInt(this.props.match.params.index-1)}
                            arr= {this.state.book.content[this.props.match.params.index - 1].sub_sections}
                        />
                    </div>
                </div>}
            </div>
        );
    }
};

export default Chapter;
