import React, { Component } from 'react';
import './list.scss';
import InnerList from './InnerList/InnerList';
import {Link} from 'react-router-dom';

class List extends Component{
    render = () => {
        const {arr} = this.props;
        return (
            <div className="sealted-list">
                <div className="list-wrapper">
                    <div className="red-line"></div>
                    { arr && arr.length > 0 &&
                        arr.map((item,index)=>(
                            <div className="list-item-wrapper" key={index}>
                                <div className="list-bullet">{ index + 1}</div>
                                <div className="list-item">
                                    <Link to={`/chapter/${index+1}`} className="list-title">{item.section}</Link>
                                    <div className="list-text">
                                        <InnerList 
                                            arr={item.sub_sections}
                                            pi={index}
                                        />
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        );
    }
}

export default List;
