import React, { Component } from 'react';
import './content.scss';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Icon } from 'antd';
import Config from '../../config/Config';

const conf = new Config()

class Content extends Component {

    state = { book: { content: [] }, playing: false, bg: false }
    componentDidMount() {
        this.getBook();
    }
    getBook() {
        let book = localStorage.getItem("book");
        if (book === null) {

            axios.get(conf.book).then(resp => {
                this.setState({
                    book: resp.data
                });
                localStorage.setItem("book", JSON.stringify(resp.data))
            })
        } else {
            this.setState({
                book: JSON.parse(book)
            });
        }
    }

    play(){
        const { book } = this.state;
        const ci = this.props.match.params.chapterIndex;
        const cti = this.props.match.params.contentIndex;
        let content = book.content[ ci - 1].sub_sections[cti -1].content;

        window.responsiveVoice.speak(content, "UK English Male");

        this.setState({
            playing: true
        });
    }

    pause(){
        responsiveVoice.cancel();

        this.setState({
            playing: false
        });
    }

    playBG(){
        var audio = document.getElementById("audio");
        audio.volume = 0.5;
        audio.play();
        this.setState({
            bg: true
        });
    }

    pauseBG(){
        var audio = document.getElementById("audio");
        audio.pause();
        this.setState({
            bg: false
        });
    }

    render() {
        const { book } = this.state;
        const ci = this.props.match.params.chapterIndex;
        const cti = this.props.match.params.contentIndex;
        return (
            <div className="content container-fluid">
                <div className="actions">
                    <Link to={`/chapter/${ci}`}>
                        Back to chapter
                        </Link>
                </div>
                {this.state.book.content.length > 0 &&
                    <div>
                        <div className="content-head">
                            <audio loop style={{visibility:'hidden'}} id="audio" src={conf.nashad} ></audio>
                            <h5>
                                {
                                    book.content[ci - 1].sub_sections[cti - 1].sub_section
                                }
                            </h5>
                            <div  style={{ float: 'right', color: "#FFF", fontSize: '20px', lineHeight: '10px' }}>
                                {
                                    !this.state.playing ?  <Icon onClick={this.play.bind(this)} type="play-circle" theme="filled" /> :
                                    <Icon onClick={this.pause.bind(this)} type="pause-circle" theme="filled" />
                                }
                                {
                                    !this.state.bg ? <Icon type="sound" onClick={this.playBG.bind(this)} style={{marginLeft: `10px`}} /> :
                                    <Icon type="sound" onClick={this.pauseBG.bind(this)} style={{marginLeft: `10px`, color: "#096dd9"}} />
                                }
                            </div>
                        </div>
                        <div className="p-cont">
                            <p>
                            {book.content[ ci - 1].sub_sections[cti -1].content}
                            </p>
                        </div>
                        { book.content[ci - 1].sub_sections.length != cti  ?
                            <Link type="button" to={`/content/${ci}/${parseInt(cti)+1}`} class="btn btn-outline-light text-dark btn-block">
                                Next Chapter
                            </Link> :
                            <Link type="button" to={`/content/${parseInt(ci) + 1}/${1}`} class="btn btn-outline-light text-dark btn-block">
                                Next Section
                            </Link>
                        }

                        <div style={{height:'50px'}}></div>
                    </div>
                }
            </div>
        );
    }
}

export default Content;