import React, {Component} from 'react';
import './splash.scss';
import Config from '../../config/Config';
import axios from 'axios';
import {Icon} from 'antd';
import {Link} from 'react-router-dom';
const conf = new Config();

class Splash extends Component {

  state = { photos: [] }
  
  componentDidMount(){
    let query = "flowers";
    let per_page = 50;
    let page = 1;

    axios({
      method: 'GET',
      url: `${conf.pexels.url}search?query=${query}&per_page=${per_page}&page=${page}`,
      headers: {
        Authorization: conf.pexels.key
      }
    }).then(resp=>{
      this.setState({
        photos: resp.data.photos
      })
    })
  }
  
  render = () => {
    return (
      <div>
          { this.state.photos.length > 0 &&
              <div className="container-fluid splash" style={{
                    backgroundImage: `url(${this.state.photos[Math.floor(Math.random() * 50) + 1 ].src.square})`
                }}>
              </div>
          }
          <div className="front">
                <div className="centered h-100 row align-items-center">
                    <div className="cen-text">
                        <h4 className="animated fadeInDown">The Sealed Nectar</h4>
                        <div className="sub-head">Prophet Mohammad's (pbuh) Biography</div>
                        <Link to="/contents" className="btn btn-outline-primary animated fadeInTop">
                          Start Reading
                          <Icon type="right" />
                        </Link>
                    </div>
                </div>
          </div>
      </div>
    );
  }
};

export default Splash;
